/* homepage */
.home {
    .top ul {
        display: none;
    }

    @media (min-width: 560px) {
        .home nav.top.open div {
            display: none;
        }
    }

    .card {
        transition: 0.4s ease;
        word-break: break-all;
        word-break: break-word;
        text-align: center;
        max-width: 46rem;
        position: relative;
        padding: 5rem 4rem 3rem 4rem;
        margin: 0 auto 1rem;
        width: 100%;
        top: 0.2rem;
    }

    @media (min-width: 560px) {
        .card {
            background-color: rgba(255, 255, 255, 0.02);
            box-shadow: -1rem 1rem 2rem 0rem rgba(0, 0, 0, 0.02), -1rem 1rem 2rem 0rem rgba(0, 0, 0, 0.1);
        }

        .card:hover {
            background-color: rgba(255, 255, 255, 0.04);
            box-shadow: -0.5rem 0.5rem 2rem 0rem rgba(0, 0, 0, 0.2), -1rem 1rem 2rem 0rem rgba(0, 0, 0, 0.2);
            top: 0rem;
        }
    }

    .card ul li {
        display: inline-block;
        margin: 0 0.3rem;
    }

    .socials i,
    .socials button {
        font-size: 2.6rem;
        padding: 1.2rem;
    }

    @media (min-width: 370px) {

        .socials i,
        .socials button {
            font-size: 2.6rem;
            padding: 1.2rem;
        }
    }
}